.container585_600 {
  display: flex;
  justify-content: space-between;
  padding: 15px;
}
.container585_600 .imageUser {
  width: 48px;
  border-radius: 50%;
  object-fit: cover;
}
.container585_600 .aboutUser {
  display: flex;
  gap: 13px;
}
.container585_600 .aboutUser .attributes {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.container585_600 .aboutUser .userValues {
  color: white;
  font-size: 18px;
  font-weight: 600;
  font-family: TTForsTrialVariable;
}
.container585_600 .aboutUser .rankUser {
  display: flex;
  align-items: center;
  color: white;
  font-size: 14px;
  font-weight: 500;
  height: 22px;
  padding: 0 10px 0 10px;
  background-color: black;
  text-align: center;
  border-radius: 90px;
  width: max-content;
  font-family: TTForsTrialVariable;
}
.container585_600 .telegramButton {
  display: flex;
  gap: 5px;
  align-items: center;
  justify-content: center;
  height: 45px;
  background-color: white;
  width: max-content;
  padding: 0 15px 0 15px;
  border-radius: 79px;
}
.container585_600 .tgIcon {
  width: 31px;
}
.container585_600 .telegramText {
  font-family: TTForsTrialVariable;
  color: black;
  font-weight: 500;
  font-size: 18px;
}
