.container {
  width: 100%;
  height: 100vh;
  overflow: scroll;
  overflow-x: hidden;
}
.container header {
  margin: 15px 30px 0 15px;
}
.container .carousel {
  margin-top: 25px;
}
.container main {
  margin: 28px 16px 100px 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.container main .doubleCard {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  gap: 10px;
}
