.container585_600 {
  padding: 20px;
  background-color: #080808;
  width: 100%;
  border-radius: 26px;
}
.container585_600 .divideLine {
  width: 100%;
  height: 2px;
  background-color: #363636;
  margin: 20px 0 20px 0;
}
.container585_600 .field {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.container585_600 .field img {
  width: 11px;
  height: 19px;
}
.container585_600 .fieldText {
  color: #f1f1f1;
  font-size: 21px;
  font-weight: 600;
  font-family: TTForsTrialVariable;
}
